@use 'variaveis';
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: variaveis.$fontPrincipal;
}

body {
    background-color: variaveis.$body;
}
li {
    list-style: none;
}

.container {
    max-width: 1024px;
    width: 100%;
    margin: 0 auto;
}
header {
    background-color: variaveis.$header;
    position: sticky;
    top: 0;
    left: 0;
}
header .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px 0;
.brandig img {
    max-width: 10%;
}
}
.menu {
    display: flex;
}
.menu li {
    font-weight: bold;
    margin-left: 24px;
}

.menu li a {
    color: variaveis.$liA;
    text-decoration: none;
}
section {
    padding: 56px 0;
}

.products {
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(3, 32.66%);
    column-gap: 1%;
    row-gap: 4%;
    width: 100%;
}
.product-item img {
    width: 100%;
    border-radius: 16px;
}
.product-button {
    display: block;
    background-color: variaveis.$productButton;
    color: variaveis.$corButton;
    text-align: center;
    padding: 8px 0;
    border-radius: 8px;
    margin-top: 8px;
    text-decoration: none;
}
.product-button:hover {
    background-color: variaveis.$pButtonHover;
}



@media screen and (max-width: variaveis.$tablet) {
    
    .container {
        max-width: 80%;
    }

    .products {
        grid-template-columns: repeat(2, 48.5%);
        column-gap: 3%;
    }
}
@media screen  and (max-width: variaveis.$mobile) {
    .products {
        display: block;
    }

    .product-item  {
        margin-bottom: 32px;
    }
    
}
